html, body, #root {
  min-height: 100vh; }

body {
  font-family: Poppins, "Helvetica Neue", Arial, Helvetica, sans-serif;
  font-weight: lighter;
  min-width: 450px; }

h1, h2, h3, h4, h5, h6 {
  margin: 10px 0;
  font-weight: 300; }

a {
  text-decoration: none;
  color: inherit; }

.aca-card__container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 40px;
  padding-top: 30px; }

.aca-card {
  height: 100%;
  display: grid;
  grid-template-rows: 200px 1fr auto; }
  .aca-card .aca-card__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block; }
  .aca-card .aca-card__title {
    text-overflow: ellipsis; }

.aca-input {
  padding: 10px;
  font-size: 0.8em;
  min-width: 300px;
  border: 0;
  border-bottom: 2px solid #eff3f6;
  outline: 0;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease; }
  .aca-input:focus {
    border-bottom-color: #b5dbf3; }

.aca-header {
  display: flex;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 20px; }
  .aca-header .aca-header__logo {
    width: 60px;
    height: 60px;
    margin-right: 10px; }
  .aca-header .aca-header__form {
    margin-left: auto; }

.aca-navbar {
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 1fr; }
  .aca-navbar .aca-navbar__item {
    text-align: center;
    padding: 10px;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    position: relative; }
    .aca-navbar .aca-navbar__item::after {
      content: '';
      position: absolute;
      display: block;
      width: 50%;
      bottom: 0;
      left: 50%;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
      border-bottom: 3px solid #eff3f6;
      -webkit-transition: all 0.3s ease;
      transition: all 0.3s ease; }
    .aca-navbar .aca-navbar__item:hover::after {
      width: 100%;
      border-bottom-color: #b5dbf3; }
    .aca-navbar .aca-navbar__item.selected {
      background: #b5dbf3; }

